import {NgModule} from "@angular/core";
import {RouterModule}  from "@angular/router";
import { HomeComponent } from "./home/home.component";
import { PeopleComponent } from "./people/people.component";
import { ProgramsComponent } from "./programs/programs.component";
import { WorkComponent } from "./work/work.component";
import { PlayComponent } from "./play/play.component";
import { ContactComponent } from "./contact/contact.component";
import { StaffComponent } from "./staff/staff.component";
import { FacultyComponent } from "./faculty/faculty.component";
import { StudentComponent } from "./student/student.component";
const appRoutes = [

{path:"",component:HomeComponent },
{path:"home",component:HomeComponent, children:[

    
] },
{path:"people",component:PeopleComponent, children:[{path:"student",component:StudentComponent},
{path:"faculty",component:FacultyComponent}
,
{path:"staff",component:StaffComponent}

    
] },
{path:"programs",component:ProgramsComponent, children:[
    
]},
{path:"work",component:WorkComponent,children:[]},
{path:"play",component:PlayComponent, children:[

]},
{path:"contact",component:ContactComponent, children:[
    
    ]}


]
@NgModule({

    imports:[
        RouterModule.forRoot(appRoutes)
    ],
exports:[RouterModule] 
})
export class AppRoutingModule{

}



